'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import Layout from '@/components/Layout';
import ClientSearchBar from '@/components/ClientSearchBar';
import { fetchClientCustomers, fetchProjectDetails, BitrixClient, BitrixCustomer, BitrixProject } from '@/lib/api/bitrix';
import { SearchResult } from '@/lib/api/search';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Skeleton } from '@/components/ui/skeleton';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Progress } from '@/components/ui/progress';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

// Mock client data - In production, this would come from API
const mockClients: Record<string, BitrixClient> = {
  '1': {
    id: '1',
    name: 'Acme Corporation',
    email: 'contact@acme.com',
    phone: '+1-555-0123',
    company: 'Acme Corp',
    status: 'Active',
    createdDate: '2024-01-15',
    lastActivity: '2024-12-20'
  },
  '2': {
    id: '2',
    name: 'Tech Solutions Ltd',
    email: 'info@techsolutions.com',
    phone: '+1-555-0456',
    company: 'Tech Solutions',
    status: 'Active',
    createdDate: '2024-02-10',
    lastActivity: '2024-12-19'
  }
};

// Mock projects with more detailed data
const mockProjects: BitrixProject[] = [
  {
    id: '1',
    title: 'Website Redesign',
    description: 'Complete website redesign with modern UI/UX',
    status: 'In Progress',
    clientId: '1',
    clientName: 'Acme Corporation',
    startDate: '2024-11-01',
    endDate: '2024-12-31',
    progress: 75,
    budget: 50000,
    assignedTo: ['John Doe', 'Jane Smith']
  },
  {
    id: '2',
    title: 'E-commerce Platform',
    description: 'Custom e-commerce solution with payment integration',
    status: 'Completed',
    clientId: '1',
    clientName: 'Acme Corporation',
    startDate: '2024-08-01',
    endDate: '2024-10-31',
    progress: 100,
    budget: 80000,
    assignedTo: ['Mike Johnson', 'Sarah Wilson']
  },
  {
    id: '3',
    title: 'Mobile App Development',
    description: 'Native mobile app for iOS and Android',
    status: 'Planning',
    clientId: '2',
    clientName: 'Tech Solutions Ltd',
    startDate: '2024-12-15',
    endDate: '2025-03-15',
    progress: 10,
    budget: 75000,
    assignedTo: ['David Brown', 'Lisa Davis']
  },
  {
    id: '4',
    title: 'Brand Identity Design',
    description: 'Complete brand identity and logo design package',
    status: 'In Progress',
    clientId: '1',
    clientName: 'Acme Corporation',
    startDate: '2024-10-15',
    endDate: '2024-12-15',
    progress: 60,
    budget: 25000,
    assignedTo: ['Alex Turner', 'Emma Wilson']
  }
];

export default function ClientDetailsPage() {
  const params = useParams();
  const router = useRouter();
  const clientId = params.id as string;
  
  const [client, setClient] = useState<BitrixClient | null>(null);
  const [customers, setCustomers] = useState<BitrixCustomer[]>([]);
  const [projects, setProjects] = useState<BitrixProject[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedResult, setSelectedResult] = useState<SearchResult | null>(null);
  const [sortBy, setSortBy] = useState<string>('all');
  const [filterStatus, setFilterStatus] = useState<string>('all');

  useEffect(() => {
    loadClientData();
  }, [clientId]);

  const loadClientData = async () => {
    try {
      setIsLoading(true);
      setError('');
      
      // Get client details
      const clientData = mockClients[clientId];
      if (!clientData) {
        setError('Client not found');
        return;
      }
      setClient(clientData);

      // Load customers and projects
      const [customerData, clientProjects] = await Promise.all([
        fetchClientCustomers(clientId),
        Promise.resolve(mockProjects.filter(p => p.clientId === clientId))
      ]);

      setCustomers(customerData);
      setProjects(clientProjects);
    } catch (err) {
      setError('Failed to load client data. Please try again.');
      console.error('Error loading client data:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSearchResultSelect = (result: SearchResult) => {
    setSelectedResult(result);
  };

  const getProjectStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'completed':
        return 'bg-green-100 text-green-800';
      case 'in progress':
        return 'bg-blue-100 text-blue-800';
      case 'planning':
        return 'bg-yellow-100 text-yellow-800';
      case 'on hold':
        return 'bg-red-100 text-red-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getProjectThumbnail = (projectId: string) => {
    // Generate different placeholder images based on project ID
    const colors = ['blue', 'green', 'purple', 'orange', 'pink'];
    const color = colors[parseInt(projectId) % colors.length];
    return `https://placehold.co/300x200/${color}/white?text=Project+${projectId}`;
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount);
  };

  const getFilteredProjects = () => {
    let filtered = projects;

    // Filter by status
    if (filterStatus !== 'all') {
      if (filterStatus === 'active') {
        filtered = filtered.filter(p => p.status === 'In Progress' || p.status === 'Planning');
      } else if (filterStatus === 'completed') {
        filtered = filtered.filter(p => p.status === 'Completed');
      }
    }

    // Sort projects
    switch (sortBy) {
      case 'name':
        filtered.sort((a, b) => a.title.localeCompare(b.title));
        break;
      case 'date':
        filtered.sort((a, b) => new Date(b.startDate).getTime() - new Date(a.startDate).getTime());
        break;
      case 'progress':
        filtered.sort((a, b) => b.progress - a.progress);
        break;
      case 'budget':
        filtered.sort((a, b) => b.budget - a.budget);
        break;
      default:
        // Keep original order
        break;
    }

    return filtered;
  };

  const getTotalProjectValue = () => {
    return projects.reduce((total, project) => total + project.budget, 0);
  };

  const getAverageProgress = () => {
    if (projects.length === 0) return 0;
    return Math.round(projects.reduce((total, project) => total + project.progress, 0) / projects.length);
  };

  if (isLoading) {
    return (
      <Layout title="Loading..." allowedRoles={['admin']}>
        <div className="space-y-6">
          <Skeleton className="h-12 w-full" />
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            {[...Array(4)].map((_, i) => (
              <Card key={i}>
                <CardContent className="p-6">
                  <Skeleton className="h-8 w-16 mb-2" />
                  <Skeleton className="h-4 w-24" />
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </Layout>
    );
  }

  if (error || !client) {
    return (
      <Layout title="Error" allowedRoles={['admin']}>
        <div className="flex flex-col items-center justify-center min-h-96">
          <Alert variant="destructive" className="max-w-md">
            <AlertDescription>{error || 'Client not found'}</AlertDescription>
          </Alert>
          <Button 
            onClick={() => router.push('/admin')} 
            className="mt-4"
            variant="outline"
          >
            Back to Admin Dashboard
          </Button>
        </div>
      </Layout>
    );
  }

  const filteredProjects = getFilteredProjects();

  return (
    <Layout title={`${client.name} - Client Details`} allowedRoles={['admin']}>
      <div className="space-y-6">
        {/* Back Button and Client Header */}
        <div className="flex items-center justify-between">
          <Button 
            onClick={() => router.push('/admin')} 
            variant="outline"
            className="mb-4"
          >
            ← Back to Admin Dashboard
          </Button>
          <div className="text-right">
            <h1 className="text-2xl font-bold text-gray-900">{client.name}</h1>
            <p className="text-gray-600">{client.company}</p>
          </div>
        </div>

        {/* Client-Specific Search Bar */}
        <div className="flex justify-center">
          <ClientSearchBar 
            clientId={clientId}
            onResultSelect={handleSearchResultSelect}
            placeholder={`Search ${client.name}'s customers and projects...`}
          />
        </div>

        {/* Search Result Display */}
        {selectedResult && (
          <Card className="border-blue-200 bg-blue-50">
            <CardHeader>
              <CardTitle className="text-lg flex items-center space-x-2">
                <span>🔍</span>
                <span>Search Result</span>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center space-x-3">
                <Badge className="capitalize">{selectedResult.type}</Badge>
                <div>
                  <p className="font-medium">{selectedResult.title}</p>
                  <p className="text-sm text-gray-600">{selectedResult.subtitle}</p>
                  {selectedResult.description && (
                    <p className="text-xs text-gray-500 mt-1">{selectedResult.description}</p>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Client Analytics */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Projects</CardTitle>
              <span className="text-2xl">📋</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{projects.length}</div>
              <p className="text-xs text-muted-foreground">
                Active projects
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Value</CardTitle>
              <span className="text-2xl">💰</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{formatCurrency(getTotalProjectValue())}</div>
              <p className="text-xs text-muted-foreground">
                Combined project value
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Avg Progress</CardTitle>
              <span className="text-2xl">📊</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{getAverageProgress()}%</div>
              <p className="text-xs text-muted-foreground">
                Across all projects
              </p>
              <Progress value={getAverageProgress()} className="h-2 mt-2" />
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Customers</CardTitle>
              <span className="text-2xl">👥</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{customers.length}</div>
              <p className="text-xs text-muted-foreground">
                Active customers
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Project Analysis */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Project Status Overview</CardTitle>
              <CardDescription>
                Distribution of project statuses
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {['Completed', 'In Progress', 'Planning', 'On Hold'].map((status) => {
                  const count = projects.filter(p => p.status === status).length;
                  const percentage = projects.length > 0 ? (count / projects.length) * 100 : 0;
                  
                  return (
                    <div key={status} className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <Badge className={getProjectStatusColor(status)}>
                          {status}
                        </Badge>
                        <span className="text-sm text-gray-600">{count} projects</span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <div className="w-24 bg-gray-200 rounded-full h-2">
                          <div
                            className="bg-blue-600 h-2 rounded-full"
                            style={{ width: `${percentage}%` }}
                          ></div>
                        </div>
                        <span className="text-sm font-medium w-12">{percentage.toFixed(0)}%</span>
                      </div>
                    </div>
                  );
                })}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Customer List</CardTitle>
              <CardDescription>
                Customers associated with this client
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {customers.length === 0 ? (
                  <p className="text-gray-500 text-center py-4">No customers found</p>
                ) : (
                  customers.map((customer) => (
                    <div key={customer.id} className="flex items-center space-x-3 p-2 bg-gray-50 rounded-lg">
                      <div className="w-8 h-8 bg-green-100 text-green-800 rounded-full flex items-center justify-center text-sm font-medium">
                        {customer.name.charAt(0)}
                      </div>
                      <div className="flex-1">
                        <p className="text-sm font-medium">{customer.name}</p>
                        <p className="text-xs text-gray-500">{customer.email}</p>
                      </div>
                      <div className="text-right">
                        <p className="text-sm font-medium">{formatCurrency(customer.totalSpent)}</p>
                        <p className="text-xs text-gray-500">{customer.projects.length} projects</p>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Projects List with Sorting and Filtering */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle>Projects</CardTitle>
                <CardDescription>
                  All projects for {client.name}
                </CardDescription>
              </div>
              <div className="flex space-x-2">
                <Select value={filterStatus} onValueChange={setFilterStatus}>
                  <SelectTrigger className="w-40">
                    <SelectValue placeholder="Filter by status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Projects</SelectItem>
                    <SelectItem value="active">Active</SelectItem>
                    <SelectItem value="completed">Completed</SelectItem>
                  </SelectContent>
                </Select>
                <Select value={sortBy} onValueChange={setSortBy}>
                  <SelectTrigger className="w-40">
                    <SelectValue placeholder="Sort by" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Default Order</SelectItem>
                    <SelectItem value="name">Project Name</SelectItem>
                    <SelectItem value="date">Start Date</SelectItem>
                    <SelectItem value="progress">Progress</SelectItem>
                    <SelectItem value="budget">Budget</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            {filteredProjects.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-gray-500">No projects found matching the current filters</p>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {filteredProjects.map((project) => (
                  <Card key={project.id} className="overflow-hidden hover:shadow-lg transition-shadow">
                    <div className="aspect-video relative">
                      <img
                        src={getProjectThumbnail(project.id)}
                        alt={project.title}
                        className="w-full h-full object-cover"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.src = `https://placehold.co/300x200/gray/white?text=${encodeURIComponent(project.title)}`;
                        }}
                      />
                      <div className="absolute top-2 right-2">
                        <Badge className={getProjectStatusColor(project.status)}>
                          {project.status}
                        </Badge>
                      </div>
                    </div>
                    <CardContent className="p-4">
                      <h3 className="font-semibold text-lg mb-2 truncate">{project.title}</h3>
                      <p className="text-sm text-gray-600 mb-3 line-clamp-2">{project.description}</p>
                      
                      <div className="space-y-2 mb-4">
                        <div className="flex justify-between text-sm">
                          <span className="text-gray-500">Client:</span>
                          <span className="font-medium">{project.clientName}</span>
                        </div>
                        <div className="flex justify-between text-sm">
                          <span className="text-gray-500">Budget:</span>
                          <span className="font-medium">{formatCurrency(project.budget)}</span>
                        </div>
                        <div className="flex justify-between text-sm">
                          <span className="text-gray-500">Timeline:</span>
                          <span className="font-medium">
                            {formatDate(project.startDate)} - {formatDate(project.endDate)}
                          </span>
                        </div>
                      </div>

                      <div className="mb-3">
                        <div className="flex justify-between text-sm mb-1">
                          <span className="text-gray-500">Progress</span>
                          <span className="font-medium">{project.progress}%</span>
                        </div>
                        <Progress value={project.progress} className="h-2" />
                      </div>

                      <div className="flex flex-wrap gap-1">
                        {project.assignedTo.slice(0, 2).map((member, index) => (
                          <Badge key={index} variant="outline" className="text-xs">
                            {member}
                          </Badge>
                        ))}
                        {project.assignedTo.length > 2 && (
                          <Badge variant="outline" className="text-xs">
                            +{project.assignedTo.length - 2} more
                          </Badge>
                        )}
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </Layout>
  );
}
